import {isElectron} from './platform'

export default class Config {
    // 
    static ENABLE_MULTI_VOIP_CALL = true;
    // 11
    static ENABLE_SINGLE_VOIP_CALL = true;
    // voipvoip window
    static ENABLE_VOIP_DEBUG_MODE = false;

    static DEFAULT_PORTRAIT_URL = 'data:image/png;base64,';
    // https(app serverim server) + wss
    // WebSockets over SSL/TLShttpstruehttpsfalse
    // true IM SERVER httpsIM SERVERhttpsIM SERVERnginxhttps
    static USE_WSS = true;

    // ROUTE80https443
    static ROUTE_PORT = 443;

    // web
    static DISABLE_LOG = false;

    // IM SERVERHOSTiphttp!
    static IM_SERVER_HOST = 'wildfirechat.net';

    // APP SERVERhttpsAPP SERVERhttps
    // app server8888
    static APP_SERVER = 'https://app.wildfirechat.net';
    static QR_CODE_PREFIX_PC_SESSION = "wildfirechat://pcsession/";
    // turn server 
    static ICE_SERVERS = [{uri: 'turn:turn.wildfirechat.net:3478', userName: 'wfchat', password: 'wfchat'}];
    static LANGUAGE = 'zh_CN';

    // appIdappKeyim server
    static WEB_APP_ID = 'web_12345678';
    static WEB_APP_KEY = '6f8348670cb11cf434451bc9e7ba72eeaf3452c8';

    static MESSAGE_ROAMING = 1;
    // 2
    static MESSAGE_ROAMING_HOUR_COUNT = 2;

    // clientId0120
    // 0 clientIdclientId
    // 1 clientIdsessionStoragesessionclientIdclientIdtab
    // 2 clientIdlocalStorageclientId
    // tokenclientId12userIdtokenuserIdtoken
    // 2web
    static CLIENT_ID_STRATEGY = 1;

    static SDK_PLATFORM_WINDOWS = 3;
    static SDK_PLATFORM_OSX = 4;
    static SDK_PLATFORM_WEB = 5;
    static SDK_PLATFORM_WX = 6;

    // ping
    static KEEP_ALIVE_INTERNAL = 180;

    /**
     * {@link wfc.onForeground}
     * {@link KEEP_ALIVE_INTERNAL}
     * 
     */
    static KEEP_ALIVE_TIMEOUT = 200;

    // html5 audio amramrmp3
    // amrmp3application/octet-stream
    // cdn
    static AMR_TO_MP3_SERVER_ADDRESS = Config.APP_SERVER + '/amr2mp3?path=';

    // 
    static SEND_MESSAGE_TIMEOUT = 20;

    // 
    static CONVERSATION_EXPIRE_TIME = 30;
    // ID
    static FILE_HELPER_ID = 'wfc_file_transfer';

    static getWFCPlatform() {
        if (isElectron()) {
            if (window.process && window.process.platform === 'darwin') {
                // osx
                return 4;
            } else {
                // windows
                return 3;
            }

        } else {
            // web
            return 5;
        }
    }

    static config(options) {
        Object.keys(options).forEach(key => {
            Config[key] = options[key];
        });
    }

    static validate() {
        let configError = true;
        if (Config.APP_SERVER === 'https://app.wildfirechat.net' && Config.IM_SERVER_HOST === 'wildfirechat.net' && Config.WEB_APP_KEY === '6f8348670cb11cf434451bc9e7ba72eeaf3452c8') {
            configError = false;
        } else if (Config.APP_SERVER !== 'https://app.wildfirechat.net' && Config.IM_SERVER_HOST !== 'wildfirechat.net' && Config.WEB_APP_KEY !== '6f8348670cb11cf434451bc9e7ba72eeaf3452c8') {
            configError = false;
        }

        console.log(`:
                    app server: ${Config.APP_SERVER}
                    im server host: ${Config.IM_SERVER_HOST}
                    key: ${Config.WEB_APP_KEY}, 6fxxx key
                    use wss: ${Config.USE_WSS}
                    route port: ${Config.ROUTE_PORT}`)
        if (configError) {
            throw new Error(', Config.APP_SERVER,Config.IM_SERVER_HOST,Config.WEB_APP_KEY ');
        }

        if (!Config.APP_SERVER.startsWith("http")) {
            throw new Error(', Config.APP_SERVER http');
        }
        if (Config.IM_SERVER_HOST.startsWith('http') || Config.IM_SERVER_HOST.indexOf(':') > -1) {
            throw new Error(', Config.IM_SERVER_HOST ip');
        }

        if (Config.USE_WSS) {
            console.warn('WSShttpshttps://docs.wildfirechat.cn/faq/web/https.html');
        }
        if (Config.USE_WSS && Config.ROUTE_PORT !== 443) {
            console.warn(`WSS${Config.ROUTE_PORT}!!`);
        }

        if (!Config.USE_WSS && Config.ROUTE_PORT !== 80) {
            console.warn(`WS${Config.ROUTE_PORT}!!`);
        }
    }
}

Config.validate();
